ol.onecheck-list {
	border-bottom: 1px solid #ddd;
	li.onecheck-list__item {
		border-bottom: none;
		padding-bottom: 0;
		&::before {
			float: left;
			min-width: 0;
			vertical-align: top;
			position: relative;
			top: 2px; } }
	.onecheck-checkbox {
		float: left;
		display: inline-block;
		vertical-align: top;
		position: relative;
		top: 3px;
		margin-bottom: 0;
		margin-left: -3px;
		margin-right: 3px;
		&__input {
			margin: 0;
			width: 17px;
			height: 17px;
			outline: none; } }
	.onecheck-text {
		float: left;
		display: inline-block;
		vertical-align: top;
		&__textarea {
			margin: 0;
			height: 15px;
			padding: 0;
			box-shadow: none;
			resize: none;
			vertical-align: middle;
			outline: none;
			border: none; } } }
